import api from './api';
import axios from 'axios';

// OSSæ“ä½œç›¸å…³API
const ossApi = {
  /**
   * è·å–OSSé…ç½®ï¼ˆä¸´æ—¶å‡­è¯ï¼‰
   * @returns {Promise<Object>} OSSé…ç½®ä¿¡æ¯
   */
  getOSSConfig() {
    return api.get('/oss/config');
  },

  /**
   * è·å–OSSä¸Šä¼ ç­–ç•¥ï¼ˆPolicyï¼‰
   * @param {string} dir ä¸Šä¼ ç›®å½•
   * @returns {Promise<Object>} ä¸Šä¼ ç­–ç•¥
   */
  getOSSPolicy(dir = 'posts/') {
    return api.get('/oss/policy', { params: { dir } });
  },

  /**
   * ç›´æ¥ä¸Šä¼ æ–‡ä»¶åˆ°é˜¿é‡Œäº‘OSS
   * @param {File} file è¦ä¸Šä¼ çš„æ–‡ä»¶å¯¹è±¡
   * @param {string} dir ä¸Šä¼ ç›®å½•
   * @returns {Promise<string>} æ–‡ä»¶çš„URL
   */
  async uploadFile(file, dir = 'posts/') {
    try {
      // 1. è·å–ä¸Šä¼ ç­–ç•¥
      const policyRes = await this.getOSSPolicy(dir);
      const policy = policyRes.policy;
      
      if (!policy) {
        throw new Error('è·å–ä¸Šä¼ ç­–ç•¥å¤±è´¥');
      }
      
      // æ£€æŸ¥å¹¶å¤„ç†hostï¼Œç¡®ä¿æ˜¯ä¸€ä¸ªæœ‰æ•ˆçš„URLå­—ç¬¦ä¸²
      let host = policy.host;
      if (typeof host === 'object') {
        console.error('Host æ˜¯ä¸€ä¸ªå¯¹è±¡è€Œä¸æ˜¯å­—ç¬¦ä¸²:', host);
        // å°è¯•ä»å¯¹è±¡ä¸­è·å–URLï¼Œæˆ–è€…æ„å»ºä¸€ä¸ªé»˜è®¤URL
        host = `https://${policy.bucket}.${policy.region}.aliyuncs.com`;
      }
      
      // ç¡®ä¿hostæ˜¯ä¸€ä¸ªæœ‰æ•ˆçš„URLå­—ç¬¦ä¸²
      if (!host.startsWith('http')) {
        host = `https://${host}`;
      }
      
      // 2. å‡†å¤‡è¡¨å•æ•°æ®
      const formData = new FormData();
      const key = `${policy.key}${file.name.substring(file.name.lastIndexOf('.'))}`;
      
      formData.append('key', key);
      formData.append('OSSAccessKeyId', policy.accessKeyId);
      formData.append('policy', policy.policyBase64);
      formData.append('signature', policy.signature);
      formData.append('success_action_status', '200');
      formData.append('file', file);
      
      console.log('æ­£åœ¨ä¸Šä¼ æ–‡ä»¶åˆ°:', host);
      
      // 3. ä¸Šä¼ æ–‡ä»¶åˆ°OSS
      await axios.post(host, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      
      // 4. è¿”å›æ–‡ä»¶URL
      let cdnHost = policy.cdnHost || host;
      if (typeof cdnHost === 'object') {
        cdnHost = host; // å¦‚æœcdnHostä¹Ÿæ˜¯å¯¹è±¡ï¼Œåˆ™ä½¿ç”¨host
      }
      
      // ç¡®ä¿URLæ ¼å¼æ­£ç¡®
      const fileUrl = `${cdnHost}/${key}`;
      console.log('æ–‡ä»¶ä¸Šä¼ æˆåŠŸï¼ŒURL:', fileUrl);
      
      return fileUrl;
    } catch (error) {
      console.error('ä¸Šä¼ æ–‡ä»¶åˆ°OSSå¤±è´¥:', error);
      throw error;
    }
  },
  
  /**
   * æ‰¹é‡ä¸Šä¼ æ–‡ä»¶åˆ°é˜¿é‡Œäº‘OSS
   * @param {Array<File>} files è¦ä¸Šä¼ çš„æ–‡ä»¶å¯¹è±¡æ•°ç»„
   * @param {string} dir ä¸Šä¼ ç›®å½•
   * @returns {Promise<Array<string>>} æ–‡ä»¶URLæ•°ç»„
   */
  async uploadMultipleFiles(files, dir = 'posts/') {
    if (!files || files.length === 0) {
      return [];
    }
    
    try {
      // åŒæ—¶è·å–ä¸€æ¬¡ä¸Šä¼ ç­–ç•¥
      const policyRes = await this.getOSSPolicy(dir);
      const policy = policyRes.policy;
      
      if (!policy) {
        throw new Error('è·å–ä¸Šä¼ ç­–ç•¥å¤±è´¥');
      }
      
      // æ£€æŸ¥å¹¶å¤„ç†hostï¼Œç¡®ä¿æ˜¯ä¸€ä¸ªæœ‰æ•ˆçš„URLå­—ç¬¦ä¸²
      let host = policy.host;
      if (typeof host === 'object') {
        console.error('Host æ˜¯ä¸€ä¸ªå¯¹è±¡è€Œä¸æ˜¯å­—ç¬¦ä¸²:', host);
        // å°è¯•ä»å¯¹è±¡ä¸­è·å–URLï¼Œæˆ–è€…æ„å»ºä¸€ä¸ªé»˜è®¤URL
        host = `https://${policy.bucket}.${policy.region}.aliyuncs.com`;
      }
      
      // ç¡®ä¿hostæ˜¯ä¸€ä¸ªæœ‰æ•ˆçš„URLå­—ç¬¦ä¸²
      if (!host.startsWith('http')) {
        host = `https://${host}`;
      }
      
      console.log('å‡†å¤‡æ‰¹é‡ä¸Šä¼ æ–‡ä»¶åˆ°:', host);
      
      // å¹¶è¡Œä¸Šä¼ æ‰€æœ‰æ–‡ä»¶
      const uploadPromises = files.map(async (file, index) => {
        try {
          const formData = new FormData();
          const key = `${policy.key}-${index}${file.name.substring(file.name.lastIndexOf('.'))}`;
          
          formData.append('key', key);
          formData.append('OSSAccessKeyId', policy.accessKeyId);
          formData.append('policy', policy.policyBase64);
          formData.append('signature', policy.signature);
          formData.append('success_action_status', '200');
          formData.append('file', file);
          
          await axios.post(host, formData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          });
          
          // å¤„ç†è¿”å›URL
          let cdnHost = policy.cdnHost || host;
          if (typeof cdnHost === 'object') {
            cdnHost = host; // å¦‚æœcdnHostä¹Ÿæ˜¯å¯¹è±¡ï¼Œåˆ™ä½¿ç”¨host
          }
          
          const fileUrl = `${cdnHost}/${key}`;
          console.log(`æ–‡ä»¶ ${index+1}/${files.length} ä¸Šä¼ æˆåŠŸ:`, fileUrl);
          return fileUrl;
        } catch (error) {
          console.error(`æ–‡ä»¶ ${index+1}/${files.length} ä¸Šä¼ å¤±è´¥:`, error);
          throw error;
        }
      });
      
      // ç­‰å¾…æ‰€æœ‰ä¸Šä¼ å®Œæˆ
      const fileUrls = await Promise.all(uploadPromises);
      console.log('æ‰€æœ‰æ–‡ä»¶ä¸Šä¼ å®Œæˆï¼ŒURLåˆ—è¡¨:', fileUrls);
      return fileUrls;
    } catch (error) {
      console.error('æ‰¹é‡ä¸Šä¼ æ–‡ä»¶åˆ°OSSå¤±è´¥:', error);
      throw error;
    }
  }
};

export default ossApi; 